.speacials_container {
  display: flex;
  flex-direction: row;
  margin: 30px 11px;
}

.speacials_slide {
  position: relative;
  margin: 0;
  width: 100%;
  background: white;
  box-shadow: 0px 1px 4px 0px rgba(0, 0, 0, 0.75);
  -webkit-box-shadow: 0px 1px 4px 0px rgba(0, 0, 0, 0.75);
  -moz-box-shadow: 0px 1px 4px 0px rgba(0, 0, 0, 0.75);
}

.speacials_heading {
  display: flex;
  justify-content: space-between;
  margin-left: 20px;
}

.speacials_heading_button {
  border: none;
  color: white;
  padding: 15px 32px;
  text-align: center;
  text-decoration: none;
  font-size: 16px;
  margin: 12px;
  cursor: pointer;
  background-color: #2874f0;
  box-shadow: 0px 1px 4px 0px rgba(0, 0, 0, 0.75);
  -webkit-box-shadow: 0px 1px 4px 0px rgba(0, 0, 0, 0.75);
  -moz-box-shadow: 0px 1px 4px 0px rgba(0, 0, 0, 0.75);
}

button:focus {
  outline: none;
}

@media (max-width: 768px) {
  .speacials_slide {
    display: none;
  }
}
